.sidebar {
    position: fixed;
    left: 0;
    top: 60px;
    bottom: 0;
    width: 80%;
    background-color: white;
    z-index: 10;
    transition: all 0.5s ease-in;
    border-top: 1px solid gray;
    transform: translateX(-100%);
    overflow: scroll;
}

.sideMenu_ul {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    padding: 30px 10px;
}

.sideMenu_ul li {
    width: 100%;
    padding: 15px 20px;
    border-radius: 0.2rem;
    display: flex;
    align-items: center;
    justify-content: start;
    gap: 1rem;
    font-size: 16px;
    box-shadow: 10px 10px 5px -8px rgba(212, 212, 212, 0.75);
    cursor: pointer;
    transition: all 0.6s;
}

.atag {
    width: 100%;
}

.sideMenu_ul li:hover {
    background-color: #075cc4f5;
    color: white;
    gap: 3rem;
}

.setting {
    animation: spin 2s infinite linear;
    color: #075cc4f5;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.wrapper {
    background-color: rgba(0, 0, 0, 0.783);
    position: fixed;
    top: 60px;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 9;
    display: none;
}

@media screen and (min-width:700px) {
    .sidebar {
        width: 30%;
    }


}

@media screen and (min-width:900px) {
    .sidebar {
        display: none;
    }

}