.water_circle_container7 {
    position: relative;
}


.water_circle_container_one_wave_one_medium7 {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    background-color: #1566cac4;
    box-shadow: inset 0 0 20px 0 #07356cf5;
    position: relative;
    border: 1px solid rgb(93, 93, 93);
    overflow: hidden;
    z-index: -1;

}

.water_circle_container_one_wave_one_medium7:after,
.water_circle_container_one_wave_one_medium7:before {
    content: "";
    position: absolute;
    width: 200%;
    height: 200%;
    top: -50%;
    left: 50%;
    background-color: #ffff;
}

.water_circle_container_one_wave_one_medium7:after,
.water_circle_container_one_wave_one_medium7:before {
    content: "";
    position: absolute;
    width: 200%;
    height: 200%;
    top: -50%;
    left: 50%;
    background-color: #ffff;
}

.water_circle_container_one_wave_one_medium7:after {
    border-radius: 45%;
    animation: water_wave_no7 5s linear infinite;
}

.water_circle_container_one_wave_one_medium7:before {
    border-radius: 35%;
    background-color: rgba(225, 225, 225, .3);
    animation: water_wave_no7 5s linear infinite;
}

@keyframes water_wave_no7 {
    0% {
        transform: translate(-50%, -50%) rotate(0deg);
        top: -30%;
    }

    50% {
        transform: translate(-50%, -50%) rotate(0deg);
        top: -50%;
    }

    100% {
        transform: translate(-50%, -50%) rotate(360deg);
        top: -30%;
    }
}


